import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import "./index.css";
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';


export default function App()
{
    const [users, setUsers] = useState([]);
    const [search, setSearch] = useState("");
    const [show, setShow] = useState([]);
    const [modal, setModal] = useState(false);

    function Getuser()
    {
        fetch('https://609e2a6033eed80017957dea.mockapi.io/users')
            .then(res => res.json())
            .then(res => setUsers(res))
    }

    useEffect(() =>
    {
        Getuser();
    }, [])

    const toggleModal = () =>
    {
        setModal(!modal)
    }


    const DeleteUser = (id) =>
    {
        fetch(`https://609e2a6033eed80017957dea.mockapi.io/users/${id}`, { method: "DELETE" })
            .then(res => res.json())
            .then(res => Getuser())
        // alert(id)
    }
    //Here the rendering work of the API is Done  
    return (
        <div classame="App">
            <div classame="container">
                <navbar>
                    <TextField style={{ width: "200px", height: "20px" }} onChange={(event) => setSearch(event.target.value)} id="outlined-basic" label="Search Food" variant="outlined" />
                </navbar>
                <div className="Render">
                    {users.filter((list) =>
                    {
                        if (search === "")
                        {
                            return list;
                        } else if (
                            list.name.toUpperCase().includes(search.toUpperCase())
                        )
                        {
                            return list;
                        }
                    }).map((list, deletind) =>
                    {
                        const { id, name, avatar, About } = list;
                        return (
                            <section>
                                <div className="Render" >
                                    <div key={id} className="img-content" >
                                        <img src={avatar} alt={id}></img>
                                    </div>
                                    <div className="detail">
                                        <h3 key={id}>{name}</h3>
                                        <div className="btn">
                                            <Button variant="contained"
                                                id={id}
                                                onClick={() =>
                                                {
                                                    if (show[id] === 1)
                                                    {
                                                        show[id] = 0;
                                                        const newArr = [...show];
                                                        setShow(newArr);
                                                    }
                                                    else
                                                    {
                                                        show[id] = 1;
                                                        const newArr = [...show];
                                                        setShow(newArr);
                                                    }
                                                }}
                                            >
                                                Show
                                            </Button>
                                            <hr />
                                            {show[id] === 1 ? <p>{About}</p> : ""}
                                        </div>
                                        {/* <p >Lorem ipsum, dolor sit amet elit. Lorem ipsum, dolor sit amet elit Lorem ipsum, dolor sit amet elit </p> */}
                                    </div>
                                    <Button style={{ backgroundColor: "lightgrey" }} onClick={() => setUsers(users.filter((el, index) => deletind !== index))} >Delete Me</Button>
                                    <Button onClick={() => DeleteUser(id)} variant="contained">Permanant Delete</Button>
                                </div>
                            </section>
                        );
                    })}
                </div>
                <button onClick={toggleModal} className="btn-modal">Add More</button>
                {modal && (
                    <div className="modal">
                        <div className="overlay">
                            <div className="modal-content">
                                <h3>Header</h3>
                                <AddUserhere Getuser={Getuser} />
                                <Button className="close" onClick={toggleModal}>X</Button>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}

function AddUserhere({ Getuser, toggleModal })
{
    const { register, handleSubmit } = useForm();
    const onSubmit = (newdata) =>
    {
        fetch('https://609e2a6033eed80017957dea.mockapi.io/users', {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({ ...newdata })
        })
            .then(res => res.json())
            .then(res => Getuser(res))
    }
    return (
        <form onSubmit={handleSubmit(onSubmit)}>
            {/* <input {...register("name")} placeholder="Give the Dish Name" /> */}
            <TextField  {...register("name")} id="standard-basic" label="Name" />

            <br />
            {/* <input {...register("ingrediant")} placeholder="Give the ingrediant name" /> */}
            <TextField  {...register("About")} id="standard-basic" label="About" />

            <br />
            {/* <input {...register("src")} placeholder="Give the Dish Photo URL" /> */}
            <TextField  {...register("avatar")} id="standard-basic" label="Photo URL" />

            {/* <input/> */}
            <input className="submit-btn" onClick={toggleModal} type="Submit" />
        </form>
    )
}









