import React from 'react'
import { BrowserRouter as Router, Switch, NavLink } from 'react-router-dom';
import "./Navbar.css"
function Navbar()
{
    return (
        <div className="nav-menu">
            <heading>
                <h2>User</h2>
            </heading>
            <ul>
                <li>
                    <NavLink style={{ textDecoration: 'none' }} exact to="/" >Home</NavLink>

                </li>
                <li>
                    <NavLink style={{ textDecoration: 'none' }} exact to="/contact" >Contact</NavLink>

                </li>
                <li>
                    <NavLink style={{ textDecoration: 'none' }} exact to="/service" >Service</NavLink>
                </li>
            </ul>
        </div>
    )
}

export default Navbar;


